// –†–µ–Ω–¥–µ—Ä –¥–µ—Å–∫—Ç–æ–ø–Ω–æ–≥–æ —Å–ø–∏—Å–∫—É –∫–∞—Ç–µ–≥–æ—Ä—ñ–π
export function renderCategories(categories) {
  // –Ω–µ –º—É—Ç—É—î–º–æ –≤—Ö—ñ–¥–Ω–∏–π –º–∞—Å–∏–≤ (—â–æ–± —É–Ω–∏–∫–Ω—É—Ç–∏ –ø–æ–±—ñ—á–Ω–∏—Ö –µ—Ñ–µ–∫—Ç—ñ–≤)
  const allCategories = ['All categories', ...categories];
  const markuoCategories = allCategories
    .map(
      category =>
        `<li class="categories-item" data-category="${category}">${category}</li>`
    )
    .join('');
  refs.categoriesList.innerHTML = markuoCategories;
}

//  –î–æ–ø–æ–º—ñ–∂–Ω–∞ —Ñ—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–µ—Ä–µ—Ç–≤–æ—Ä–µ–Ω–Ω—è —Ç–µ–∫—Å—Ç—É —É —Ñ–æ—Ä–º–∞—Ç "Title Case"
// –í–æ–Ω–∞ –ø–µ—Ä–µ—Ç–≤–æ—Ä—é—î "SOME TITLE" -> "Some Title"
function toTitleCase(str) {
  // –Ø–∫—â–æ —Ä—è–¥–æ–∫ –ø–æ—Ä–æ–∂–Ω—ñ–π, –ø–æ–≤–µ—Ä—Ç–∞—î–º–æ –π–æ–≥–æ –±–µ–∑ –∑–º—ñ–Ω
  if (!str) {
    return '';
  }
  // –ü–µ—Ä–µ—Ç–≤–æ—Ä—é—î–º–æ —Ä—è–¥–æ–∫ –≤ –Ω–∏–∂–Ω—ñ–π —Ä–µ–≥—ñ—Å—Ç—Ä, —Ä–æ–∑–±–∏–≤–∞—î–º–æ –Ω–∞ —Å–ª–æ–≤–∞,
  // —Ä–æ–±–∏–º–æ –ø–µ—Ä—à—É –ª—ñ—Ç–µ—Ä—É –∫–æ–∂–Ω–æ–≥–æ —Å–ª–æ–≤–∞ –≤–µ–ª–∏–∫–æ—é —ñ –∑'—î–¥–Ω—É—î–º–æ –Ω–∞–∑–∞–¥.
  return str
    .toLowerCase()
    .split(' ')
    .map(word => word.charAt(0).toUpperCase() + word.slice(1))
    .join(' ');
}

// –†–µ–Ω–¥–µ—Ä —Å–ø–∏—Å–∫—É –∫–Ω–∏–≥ (–æ—Ç—Ä–∏–º—É—î—à –≤–∂–µ –≤—ñ–¥—Ñ—ñ–ª—å—Ç—Ä–æ–≤–∞–Ω–∏–π –∞–±–æ –æ–±—Ä—ñ–∑–∞–Ω–∏–π –º–∞—Å–∏–≤)
export function renderBookCardlist(topbooks) {
  const markupBooks = topbooks
    .map(
      ({
        _id,
        book_image,
        description = 'description || title',
        title,
        author,
        price,
      }) => {
        // –ó–ú–Ü–ù–ï–ù–û: –§–æ—Ä–º–∞—Ç—É—î–º–æ –Ω–∞–∑–≤—É –ø–µ—Ä–µ–¥ –≤—Å—Ç–∞–≤–∫–æ—é –≤ HTML
        const formattedTitle = toTitleCase(title);
        // –û—Ç—Ä–∏–º—É—î–º–æ —Ç—ñ–ª—å–∫–∏ —Ü—ñ–ª—É —á–∞—Å—Ç–∏–Ω—É —Ü—ñ–Ω–∏
        const formattedPrice = parseInt(price);
        return `<li class="books-card" data-id="${_id}">
        <img loading="lazy" class="books-card-image" src="${book_image}" alt="${description}" />
         <div class="books-card-text-wrapper">

            <div class="books-card-info">
              <h3 class="books-card-title">${formattedTitle}</h3>
              <p class="books-card-author">${author}</p>
            </div>

            <p class="books-card-price">$${formattedPrice}</p>

          </div>
        <button class="books-card-btn" type="button">Learn More</button>
      </li>`;
      }
    )
    .join('');
  refs.bookCardList.innerHTML = markupBooks;
}

