import { refs } from './refs.js';
import {
  getCategoryList,
  getTopBooks,
  getBooksByCategory,
  getBookById,
} from './api-functions.js';
import {
  renderCategories,
  renderCategoryDropList,
  renderBookCardlist,
  initDropdownBehavior,
} from './render-functions.js';
import { initBookModal } from './modal-book.js';

// ====== –ì–ª–æ–±–∞–ª—å–Ω—ñ –∑–º—ñ–Ω–Ω—ñ ======
let currentBooks = []; // —É—Å—ñ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω—ñ –∫–Ω–∏–≥–∏
let currentCategory = 'All categories'; // –ø–æ—Ç–æ—á–Ω–∞ –∫–∞—Ç–µ–≥–æ—Ä—ñ—è
// –ó–º—ñ–Ω–Ω–∞ –¥–ª—è –≤—ñ–¥—Å—Ç–µ–∂–µ–Ω–Ω—è –∫—ñ–ª—å–∫–æ—Å—Ç—ñ –≤–∏–¥–∏–º–∏—Ö –∫–Ω–∏–≥
let visibleBooksCount = 0;
//–ö–æ–Ω—Å—Ç–∞–Ω—Ç–∞ –¥–ª—è –∫—ñ–ª—å–∫–æ—Å—Ç—ñ –∫–Ω–∏–≥, —â–æ –¥–æ–¥–∞—é—Ç—å—Å—è –∑–∞ –∫–ª—ñ–∫
const BOOKS_PER_PAGE = 4;

// ====== –§—É–Ω–∫—Ü—ñ—è –≤–∏–∑–Ω–∞—á–∞—î, —Å–∫—ñ–ª—å–∫–∏ –∫–Ω–∏–≥ –ø–æ–∫–∞–∑–∞—Ç–∏ ======
function getInitialLimit() {
  return window.innerWidth < 768 ? 10 : 24;
}

//  –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –≤–∏–¥–∏–º–æ—Å—Ç—ñ –∫–Ω–æ–ø–∫–∏ "Show More"
function updateShowMoreButton() {
  // –Ø–∫—â–æ –≤–∏–¥–∏–º–∏—Ö –∫–Ω–∏–≥ —Å—Ç—ñ–ª—å–∫–∏ –∂ –∞–±–æ –±—ñ–ª—å—à–µ, –Ω—ñ–∂ —É—Å—å–æ–≥–æ, —Ö–æ–≤–∞—î–º–æ –∫–Ω–æ–ø–∫—É
  if (visibleBooksCount >= currentBooks.length) {
    refs.showMoreBtn.classList.add('is-hidden');
  } else {
    // –Ü–Ω–∞–∫—à–µ ‚Äî –ø–æ–∫–∞–∑—É—î–º–æ
    refs.showMoreBtn.classList.remove('is-hidden');
  }
}

// ====== –§—É–Ω–∫—Ü—ñ—è –≤—ñ–¥–º–∞–ª—é–≤–∞–Ω–Ω—è –∫–Ω–∏–≥ (–±–µ–∑ –Ω–æ–≤–∏—Ö –∑–∞–ø–∏—Ç—ñ–≤) ======
function renderVisibleBooks() {
  // –ü–µ—Ä–µ–∫–æ–Ω–∞—î–º–æ—Å—å, —â–æ visibleBooksCount –Ω–µ –±—ñ–ª—å—à–µ, –Ω—ñ–∂ –¥–æ—Å—Ç—É–ø–Ω–æ
  visibleBooksCount = Math.min(visibleBooksCount, currentBooks.length);

  // –ë–µ—Ä–µ–º–æ –ø–æ—Ç—Ä—ñ–±–Ω—É –∫—ñ–ª—å–∫—ñ—Å—Ç—å –∫–Ω–∏–≥ –∑ –º–∞—Å–∏–≤—É
  const booksToRender = currentBooks.slice(0, visibleBooksCount);
  renderBookCardlist(booksToRender);

  // –û–Ω–æ–≤–ª—é—î–º–æ –ª—ñ—á–∏–ª—å–Ω–∏–∫–∏
  if (refs.booksShown) {
    refs.booksShown.textContent = visibleBooksCount;
  }
  if (refs.booksTotal) {
    refs.booksTotal.textContent = currentBooks.length;
  }

  // –û–Ω–æ–≤–ª—é—î–º–æ —Å—Ç–∞–Ω –∫–Ω–æ–ø–∫–∏ –ø—ñ—Å–ª—è –∫–æ–∂–Ω–æ–≥–æ —Ä–µ–Ω–¥–µ—Ä—É
  updateShowMoreButton();

  // –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑—É—î–º–æ –º–æ–¥–∞–ª–∫—É –¥–ª—è –Ω–æ–≤–∏—Ö –∫–∞—Ä—Ç–æ–∫
  initBookModal();
}

// ====== –ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó (–æ–¥–∏–Ω –∑–∞–ø–∏—Ç) ======
async function loadBooksByCategory(category) {
  try {
    currentCategory = category;
    let booksData;

    if (category === 'All categories' || category === 'All' || !category) {
      booksData = await getTopBooks();
    } else {
      booksData = await getBooksByCategory(category);
    }

    // –ó–±–µ—Ä—ñ–≥–∞—î–º–æ —Ä–µ–∑—É–ª—å—Ç–∞—Ç
    currentBooks = booksData;
    //  –í—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ –ø–æ—á–∞—Ç–∫–æ–≤—É –∫—ñ–ª—å–∫—ñ—Å—Ç—å –≤–∏–¥–∏–º–∏—Ö –∫–Ω–∏–≥
    visibleBooksCount = getInitialLimit();
    // –†–µ–Ω–¥–µ—Ä–∏–º–æ –ø–µ—Ä—à—É –ø–æ—Ä—Ü—ñ—é –∫–Ω–∏–≥
    renderVisibleBooks();
  } catch (error) {
    console.error('loadBooksByCategory error:', error);
  }
}

//  –û–±—Ä–æ–±–Ω–∏–∫ –¥–ª—è –∫–Ω–æ–ø–∫–∏ "Show More"
function handleShowMore() {
  // –ó–±—ñ–ª—å—à—É—î–º–æ –ª—ñ—á–∏–ª—å–Ω–∏–∫ –≤–∏–¥–∏–º–∏—Ö –∫–Ω–∏–≥
  visibleBooksCount += BOOKS_PER_PAGE;
  // –ü–µ—Ä–µ–º–∞–ª—å–æ–≤—É—î–º–æ —Å–ø–∏—Å–æ–∫ –∑ –Ω–æ–≤–æ—é –∫—ñ–ª—å–∫—ñ—Å—Ç—é
  renderVisibleBooks();
}

// ====== –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è –∫–∞—Ç–µ–≥–æ—Ä—ñ–π ======
async function initCategories() {
  try {
    const categories = await getCategoryList();
    renderCategoryDropList(categories);
    renderCategories(categories);
    initDropdownBehavior();

    // —Å–ª—É—Ö–∞—á—ñ –¥–ª—è dropdown
    refs.dropdownMenu.addEventListener('click', e => {
      const item = e.target.closest('.dropdown-item');
      if (!item) return;
      const category = item.dataset.category || item.textContent.trim();
      refs.dropdownToggle.textContent = item.textContent.trim();
      loadBooksByCategory(category);
    });

    // —Å–ª—É—Ö–∞—á—ñ –¥–ª—è desktop —Å–ø–∏—Å–∫—É
    refs.categoriesList.addEventListener('click', e => {
      const item = e.target.closest('.categories-item');
      if (!item) return;
      const category = item.dataset.category || item.textContent.trim();

      // –≤—ñ–∑—É–∞–ª—å–Ω–æ –∞–∫—Ç–∏–≤–Ω–∞ –∫–∞—Ç–µ–≥–æ—Ä—ñ—è
      refs.categoriesList
        .querySelectorAll('.categories-item')
        .forEach(el => el.classList.remove('active'));
      item.classList.add('active');

      loadBooksByCategory(category);
    });
  } catch (error) {
    console.error('initCategories error:', error);
  }
}

// ====== –ü–æ—á–∞—Ç–∫–æ–≤–∞ —ñ–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è ======
async function initBooks() {
  await initCategories();
  await loadBooksByCategory('All categories');
}

// ====== –ü—Ä–∏–≤'—è–∑–∫–∞ —Å–ª—É—Ö–∞—á—ñ–≤ ======

// –ù–û–í–ï: –î–æ–¥–∞—î–º–æ —Å–ª—É—Ö–∞—á–∞ –Ω–∞ –∫–Ω–æ–ø–∫—É "Show More"
refs.showMoreBtn.addEventListener('click', handleShowMore);
// ====== –ü—Ä–∏ –∑–º—ñ–Ω—ñ —à–∏—Ä–∏–Ω–∏ –µ–∫—Ä–∞–Ω–∞ ‚Äî —Ç—ñ–ª—å–∫–∏ –ø–µ—Ä–µ—Ä–µ–Ω–¥–µ—Ä –ª–æ–∫–∞–ª—å–Ω–∏—Ö –¥–∞–Ω–∏—Ö ======
window.addEventListener('resize', () => {
  renderVisibleBooks(); // –±–µ–∑ –∑–∞–ø–∏—Ç—ñ–≤, —Ç—ñ–ª—å–∫–∏ –∑–º—ñ–Ω–∞ –∫—ñ–ª—å–∫–æ—Å—Ç—ñ
});

initBooks();
